$primary-color: #000000;
$secondary-color: #ffffff;

// Stage 1
$stage1-primary-color: #519E8A;
$stage1-secondary-color: #7EB09B;
$stage1-tert-color: #ECBEB4;
$stage1-bg-color: #476A6F;

// Stage 2
$stage2-primary-color: #EA9010;
$stage2-secondary-color: #90BE6D;
$stage2-tert-color: #EAEFBD;
$stage2-bg-color: #37371F;

// Stage 3
$stage3-primary-color: #70CAD1;
$stage3-secondary-color: #8EE3F5;
$stage3-tert-color: #B08EA2;
$stage3-bg-color: #3E517A;

// Stage 4
$stage4-primary-color: #574B60;
$stage4-secondary-color: #7D8491;
$stage4-tert-color: #EAF0CE;
$stage4-bg-color: #3F334D;

// Stage Z
$stageZ-primary-color: #E43F6F;
$stageZ-secondary-color: #F56476;
$stageZ-tert-color: #008DD5;
$stageZ-bg-color: #DFBBB1;

body {
  margin: 0;
  padding: 0;
  font-family: 'Libre Baskerville', Helvetica, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $primary-color;
  color: $secondary-color;
}

.wrapper {
  text-align: center;
  width: 100%;
  justify-content: center;
  overflow-x: hidden;
}

.timer {
  font-family: monospace;
  font-size: 32px;
  text-align: center;
  -webkit-animation: fadeInUp 1.5s ease-in;
  -moz-animation: fadeInUp 1.5s ease-in;
  -o-animation: fadeInUp 1.5s ease-in;
  -ms-animation: fadeInUp 1.5s ease-in;
  animation: fadeInUp 1.5s ease-in;
  margin-top: 6px;
  margin-bottom: 8px;
  padding-top: 32px;
  padding-bottom: 32px;
  cursor: pointer;
  max-width: 300px;
  display: inline-block;
}

@keyframes fadeInUp {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadeInUp {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadeInUp {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadeInUp {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadeInUp {
  from { opacity: 0; }
  to   { opacity: 1; }
}