.main-title {
  font-size: 76px;
  margin: 4px 4px 4px 4px;
  text-align: center;
  letter-spacing: 4px;
}

@keyframes fadeOut {
  from { opacity: 1; }
  to   { opacity: 0; }
}

/* Firefox < 16 */
@-moz-keyframes fadeOut {
  from { opacity: 1; }
  to   { opacity: 0; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadeOut {
  from { opacity: 1; }
  to   { opacity: 0; }
}

/* Internet Explorer */
@-ms-keyframes fadeOut {
  from { opacity: 1; }
  to   { opacity: 0; }
}

/* Opera < 12.1 */
@-o-keyframes fadeOut {
  from { opacity: 1; }
  to   { opacity: 0; }
}

.content {
  width: 100%;
  position: relative;
  text-align: center;
}